<template>
  <div class="index">
    <!-- 头部 -->
    <header style="height:100px;background-color:#545c64;text-align:right;line-height:100px;border-bottom:1px solid white">
      <img src="../assets/logo1.png" alt="" style="float:left;margin-top:35px;margin-left:20px">
      <span style="margin-right:50px;color:white">{{admin}}</span>
    </header>


    <div style="display:flex;">

      <div style="width:15%;">
        <el-menu
        :default-active="index"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      style="height:908px"
      router>
      <el-menu-item index="server" @click="go('server')">
        <i class="el-icon-menu"></i>
        <span slot="title">设备管理</span>
      </el-menu-item>
      <el-menu-item index="users" @click="go('users')">
        <i class="el-icon-menu"></i>
        <span slot="title">负责人管理</span>
      </el-menu-item>
      <el-menu-item index="fault" @click="go('fault')">
        <i class="el-icon-menu"></i>
        <span slot="title">故障记录</span>
      </el-menu-item>
      <!-- <el-menu-item index="addServer" @click="go('addServer')">
        <i class="el-icon-document"></i>
        <span slot="title">增加设备</span>
      </el-menu-item> -->
      <el-menu-item index="manualTest" @click="go('manualTest')">
        <i class="el-icon-setting"></i>
        <span slot="title">测试管理</span>
      </el-menu-item>
      <!-- <el-menu-item index="repair">
        <i class="el-icon-setting"></i>
        <span slot="title">修复管理</span>
      </el-menu-item> -->
    </el-menu>

    </div>

      <div style="width:85%;margin-left:5px">
        <div  v-if="isShow">
          <div>
            <div style="font-size:20px;font-weight:bold;height:50px;line-height:50px;width:48%;float:left;">设备管理</div>
            <div style="float:right;margin-right:10px;height:50px;line-height:50px;width:50%;text-align:right">
              <el-input v-model="einput" placeholder="请输入设备名" style="width:200px"></el-input><el-button type="info" @click="cheack">查询</el-button>
              <el-button type="primary" @click="addIsshow">添加</el-button>
            </div>
          </div>

          <el-table
        :data="tableData"
        border
    style="width: 100%;">
    <el-table-column
      prop="eName"
      label="设备名"
      
      >
    </el-table-column>
    <el-table-column
      prop="eId"
      label="设备ID">
    </el-table-column>
    <el-table-column
      prop="oIP"
      label="设备外网IP">
    </el-table-column>
    <el-table-column
      prop="iIP"
      label="设备内网IP">
    </el-table-column>
    <el-table-column
      prop="port"
      label="设备开放端口">
    </el-table-column>

    <el-table-column
      prop="domain"
      label="设备域名">
    </el-table-column>

    <el-table-column
      prop="chargePerson"
      label="设备负责人">
    </el-table-column>

    <!-- <el-table-column
      prop="services"
      label="服务">
    </el-table-column> -->

    <el-table-column
      prop="methods"
      label="操作"
      width="200">

      <template slot-scope="scope">
        <el-button
          size="mini  "
          @click="handleEdit(scope.$index, scope.row)">管理</el-button>
        <el-button
          size="mini  "
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>

    </el-table-column>

    </el-table>

  <!-- 增加设备 -->
      <el-dialog
        title="增加设备"
        :visible.sync="dialogVisible"
        width="30%"
    >
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item label="设备名称">
            <el-input v-model="form.eName"></el-input>
        </el-form-item>
    
        <el-form-item label="设备外网IP">
            <el-input v-model="form.oIP"></el-input>
        </el-form-item>
    
        <el-form-item label="设备内网IP">
            <el-input v-model="form.iIP"></el-input>
        </el-form-item>

        <el-form-item label="设备开放端口">
            <el-input v-model="form.port"></el-input>
        </el-form-item>

        <el-form-item label="设备域名">
            <el-input v-model="form.domain"></el-input>
        </el-form-item>

         <el-form-item label="负责人">
            <el-select v-model="form.chargePerson" placeholder="请选择负责人">
            <el-option :label="item.cName" :value="item.cName" v-for="(item,index) in person" :key="index"></el-option>
            </el-select>
        </el-form-item>
        <!-- 自定义配置信息 -->
        <div>
            <div style="text-align:center;background-color:#545c64;color:white;line-height:50px">自定义配置信息</div>
            属性名:<el-input v-model="obj.key" placeholder="请输入内容"></el-input>
            属性值<el-input v-model="obj.value" placeholder="请输入内容"></el-input>
        </div>

        <!-- <el-form-item style="margin-top:20px;text-align:right">
            <el-button type="primary" @click="Submit">立即添加</el-button>
        </el-form-item> -->

    </el-form>


        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addMessage">确 定</el-button>
        </span>
    </el-dialog>

        </div>

    <router-view v-else></router-view>
      </div>

    </div>
  </div>
</template>



<script>
export default {
  name: 'Index',
  data () {
    return {
      admin:'nanjun',
      //控制内容显示
      isShow:true,
      keys:[],
      index:'',    
      tableData:[],
      einput:'',
      dialogVisible:false,
      form: {
          eName: '',
          oIP: '',
          iIP: '',
          port: '',
          domain: '',
          chargePerson: '',
          eMessage:''
      },
      obj:{
            'key':'',
            'value':''
      },
      person:[]
    }
  },
  methods:{
    //管理
    handleEdit(index, row) {
      console.log(6262652)
      localStorage.setItem('data',JSON.stringify(row))
        this.$router.push({
          name:'EditServer',
        }).then(()=>{
          localStorage.setItem('server',row)
          window.location.reload()
        })
    },
    //删除
    handleDelete(index, row) {
      console.log(index, row);
      this.$axios.delete('http://192.168.1.155:8888/equipmentController/'+row.eId).then((msg)=>{
        
        if (msg.data) {
          this.$message({
              message: '恭喜你，删除成功',
              type: 'success'
          });
          window.location.reload()
          }else{
            this.$message.error('删除失败');
          }
        })
    },
    //查询设备
    cheack(){
      let obj = {
        'eName':this.einput
      }
      this.$axios.post('http://192.168.1.155:8888/equipmentController/query',obj).then((msg)=>{
        if (!msg.data.length) {
          //没有数据
          this.$message({
            message: '没有查找到数据',
            type: 'warning'
          });
        }else{
          this.tableData = msg.data
        }
        this.einput = ''
      })
    },
    handleOpen(key, keyPath) {
      // console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      // console.log(key, keyPath);
    },
    //页面路由控制
    go(value){
      if (value == 'server') {
        this.$router.push('/')
        this.isShow = true
      }else{
        this.isShow = false
      }
    },
    //提交添加信息表单
    addMessage(){
      this.dialogVisible = false
      this.form.eMessage = JSON.stringify([this.obj])
      this.$axios.post('http://192.168.1.155:8888/equipmentController/insert',this.form).then((msg)=>{
        if (msg.data) {
          this.$message({
            message: '恭喜你，添加成功',
            type: 'success'
          });
          window.location.reload()
        }else{
          this.$message.error('添加失败');
        }
      })

    },
    //展示添加设备信息会话框
    addIsshow(){
      this.dialogVisible = true
    }
  },
  created(){
    if (this.$route.path !== '/') {
        this.isShow = false
        this.index = this.$route.path.substring(1)
      }else{
        this.isShow = true
        this.index = 'server'
    }
  },
  mounted(){
    this.$axios.get('http://192.168.1.155:8888/equipmentController').then((msg)=>{
      this.tableData = msg.data
    })
    //获取负责人列表
    this.$axios.get('http://192.168.1.155:8888/CPController').then((msg)=>{
      this.person = msg.data
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



